.cities-section-five-main-container {
  display: flex;
  flex-direction: row;
  max-width: 100vw;
  padding: 0px 50px 50px 50px;
  /* padding: 50px 50px; */
  position: relative;
  min-height: 60px;
  flex-wrap: wrap-reverse;
}

.cities-section-five-main-container-big-circle{

  position: absolute;
  background-image: url("https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692782306/City/section-five/BigCircle_ufier6.webp");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  left: -300px;
  min-width: 60%;
  min-height: 60vh;

}

.cities-section-five-main-container-sub-container-one {
  flex: 1;
  display: flex;
  justify-content: center;
  align-self: center;
  position: relative;
}

.cities-section-five-main-container-sub-container-one-triangle {
  position: absolute;
  background-image: url("https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692782308/City/section-five/triangle_pqfsjy.webp");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  top: 0px;
  transform: rotate(5deg);
  right: 6%;
  width: 27.5px;
  height: 22.5px;
}

.cities-section-five-main-container-sub-container-one-circle {
  position: absolute;
  bottom: 5px;
  left: 10%;
  background-image: url("https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692782307/City/section-five/circle-on-top_ayo2xy.webp");
  background-size: cover;
  background-repeat: no-repeat;
  min-width: 30px;
  min-height: 30px;
}

.cities-section-five-main-container-sub-container-one img {
  max-width: 400px;
  max-height: 300px;
}

.cities-section-five-main-container-sub-container-two {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}

.cities-section-five-main-container-sub-container-two-circle {
  position: absolute;
  top: 5px;
  right: 22.5%;
  background-image: url("https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692782307/City/section-five/circle-on-top_ayo2xy.webp");
  background-size: cover;
  background-repeat: no-repeat;
  min-width: 30px;
  min-height: 30px;
}

.cities-section-five-main-container-sub-container-two-triangle {
  position: absolute;
  background-image: url("https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692782308/City/section-five/triangle_pqfsjy.webp");
  background-position: center;
  background-size: contain;
  bottom: 0px;
  transform: rotate(5deg);
  right: 0%;
  width: 35px;
  height: 30px;
}

.cities-section-five-main-container-sub-container-two-heading {
  margin-bottom: 10px;
}

.cities-section-five-main-container-sub-container-two-paragraph p {
  text-align: justify;
  line-height: 1.6;
}

.cities-section-five-main-container-sub-container-two-button {
  margin-top: 10px;
}

.cities-section-five-main-container-sub-container-two-button button {
  background-color: #612d91;
  color: #fff;
  border: none;
  font-size: medium;
  padding: 7.5px 10px;
}

@media (max-width: 768px) {

  .cities-section-five-main-container-big-circle {
    left: -170px;
  }
}

@media (max-width: 425px) {
  .cities-section-five-main-container-sub-container-one-triangle {
    top: 25px;
    left: 0%;
  }

  .cities-section-five-main-container-sub-container-one-circle {
    bottom: -20px;
    left: -5%;
  }

  .cities-section-five-main-container-big-circle {
    min-height: 30vh;
    bottom: 38px;
    left: -80px;
  }
}

@media (max-width: 375px) {
  .cities-section-five-main-container-sub-container-one img {
    max-width: 275px;
  }

  .cities-section-five-main-container-big-circle {
    min-height: 25vh;
    bottom: 30px;
  }
}

@media (max-width: 320px) {
    
  .cities-section-five-main-container {
    padding: 0 30px;
    padding-top: 50px;
  }

  .cities-section-five-main-container-sub-container-one img {
    max-width: 250px;
    max-height: 300px;
  }

  .cities-section-five-main-container-sub-container-one-circle {
    bottom: 15px;
    left: -5%;
  }

  .cities-section-five-main-container-big-circle {
    min-height: 15vh;
    bottom: 10px;
  }
}
