.cities-section-three-main-container {
  display: flex;
  flex-direction: column;
  padding: 50px 50px;
  position: relative;
  min-height: 60px;
}

.cities-section-three-main-container-circle {
  position: absolute;
  top: 30px;
  left: 37.5%;
  background-image: url("https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692780147/City/city-section-three/circle-on-top_smvveh.webp");
  background-size: cover;
  background-repeat: no-repeat;
  min-width: 30px;
  min-height: 30px;
}

.cities-section-three-main-container-first-triangle {
  position: absolute;
  background-image: url("https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692780147/City/city-section-three/triangle_c6rvt1.webp");
  background-position: center;
  background-size: contain;
  top: 50px;
  transform: rotate(5deg);
  right: 5%;
  width: 35px;
  height: 30px;
}

.cities-section-three-main-container-side-circle {
  position: absolute;
  top: 45%;
  transform: translateY(-50%);
  left: -15%;
  background-image: url("https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692780147/City/city-section-three/circle2_nsrhtp.webp");
  background-size: cover;
  background-repeat: no-repeat;
  min-width: 325px;
  min-height: 325px;
}

.cities-section-three-main-container-first-sub-section {
  flex: 1;
  /* background-color: brown; */
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.cities-section-three-main-container-first-sub-section-part-one {
  flex: 1;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.cities-section-three-main-container-first-sub-section-heading {
  margin-bottom: 10px;
}

.cities-section-three-main-container-first-sub-section-paragraph p {
  text-align: justify;
  line-height: 1.6;
}

.cities-section-three-main-container-first-sub-section-buttons {
  display: flex;
  flex-direction: row;
  gap: 5px;
  margin-top: 10px;
}

.cities-section-three-main-container-first-sub-section-buttons button {
  background-color: #612d91;
  color: #fff;
  border: none;
  font-size: medium;
  padding: 7.5px 10px;
}

.cities-section-three-main-container-first-sub-section-part-two {
  flex: 1;
  display: flex;
  justify-content: center;
  align-self: center;
  z-index: 5;
}

.cities-section-three-main-container-first-sub-section-part-two img {
  max-width: 400px;
  max-height: 300px;
}

.cities-section-three-main-container-second-sub-section {
  flex: 1;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap-reverse;
  position: relative;
}

.cities-section-three-main-container-second-sub-section-part-one {
  flex: 1;
  display: flex;
  justify-content: center;
  align-self: center;
  position: relative;
}

.cities-section-three-main-container-second-triangle {
  position: absolute;
  top: 60%;
  left: 85%;
  background-image: url("https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692780147/City/city-section-three/triangle_c6rvt1.webp");
  background-position: center;
  background-size: contain;
  transform: rotate(30deg);
  width: 35px;
  height: 30px;
}

.cities-section-three-main-container-second-sub-section-part-one img {
  max-width: 400px;
  max-height: 300px;
}

.cities-section-three-main-container-second-sub-section-part-two {
  flex: 1;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.cities-section-three-main-container-second-sub-section-heading {
  margin-bottom: 10px;
  z-index: 4;
  background-color: #fff;
}

.cities-section-three-main-container-second-sub-section-paragraph p {
  text-align: justify;
  line-height: 1.6;
  background-color: #fff;
}

.cities-section-three-main-container-second-sub-section-buttons {
  display: flex;
  flex-direction: row;
  gap: 5px;
  margin-top: 10px;
}

.cities-section-three-main-container-second-sub-section-buttons button {
  background-color: #612d91;
  color: #fff;
  border: none;
  font-size: medium;
  padding: 7.5px 10px;
}

.cities-section-three-main-container-second-circle {
  position: absolute;
  top: 30px;
  left: -2%;
  background-image: url("https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692780147/City/city-section-three/circle-on-top_smvveh.webp");
  background-size: cover;
  background-repeat: no-repeat;
  min-width: 30px;
  min-height: 30px;
}

@media (max-width: 425px) {
  .cities-section-three-main-container-circle {
    top: 15px;
    left: 25%;
  }

  .cities-section-three-main-container-side-circle {
    top: 50%;
    transform: translateY(-50%);
    left: -25%;
    min-width: 250px;
    min-height: 250px;
  }

  .cities-section-three-main-container-second-triangle {
    top: 95%;
    left: 92.5%;
  }
}

@media (max-width: 375px) {
  .cities-section-three-main-container-first-sub-section-part-two img {
    max-width: 275px;
  }

  .cities-section-three-main-container-second-sub-section-part-one img {
    max-width: 275px;
  }
}

@media (max-width: 320px) {

  .cities-section-three-main-container-second-triangle {
    top: 82.5%;
  }

  .cities-section-three-main-container {
    padding: 0 30px;
    padding-top: 50px;
  }

  .cities-section-three-main-container-first-sub-section-part-two img {
    max-width: 250px;
    max-height: 300px;
  }

  .cities-section-three-main-container-second-sub-section-part-one img {
    max-width: 250px;
    max-height: 300px;
  }
}
