.city-section-one-main-container {
  max-width: 100vw;
  height: fit-content;
  display: flex;
  flex-direction: row;
  padding: 0 50px;
  padding-top: 50px;
  flex-wrap: wrap;
}

.city-section-one-sub-container-one {
  flex: 1;
}

.city-section-one-sub-container-one h2 {
  font-weight: 300;
}

.city-section-one-sub-container-one-heading-one {
  border-bottom: 4px solid #612d91;
}

.city-section-one-sub-container-one-heading-one h1 {
  display: inline;
  font-weight: 600;
}

.city-section-one-sub-container-one-heading-two h1 {
  display: inline;
  font-weight: 600;
}

.city-section-one-sub-container-one-paragraph {
  margin: 15px 0px;
  padding-right: 10px;
}

.city-section-one-sub-container-one-paragraph p {
  line-height: 1.4;
  font-size: medium;
  text-align: justify;
}

.city-section-one-sub-container-one-triangle {
  position: relative;
  width: 100%;
  height: 50px;
}

.city-section-one-sub-container-one-triangle img {
  position: absolute;
  left: 20px;
  width: 35px;
  height: 30px;
  transform: rotate(110deg);
}

.city-section-one-sub-container-two {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.city-section-one-sub-container-two-triangle-one {
  flex: 1;
  position: relative;
  min-height: 30px;
}

.city-section-one-sub-container-two-triangle-one img {
  position: absolute;
  top: -7.5px;
  right: 35%;
}

.city-section-one-sub-container-two-image {
  flex: 8;
  display: flex;
  align-items: center;
  justify-content: center;
}

.city-section-one-sub-container-two-image img {
  max-width: 400px;
  max-height: 300px;
}

.city-section-one-sub-container-two-triangle-two {
  flex: 1;
  min-height: 30px;
  position: relative;
}

.city-section-one-sub-container-two-triangle-two img {
  position: absolute;
  right: 5%;
}

@media (max-width: 375px) {
    .section-one-sub-container-two-image img {
        max-width: 275px;
    }
}

@media (max-width: 320px) {
    
    .city-section-one-main-container {

        padding: 0 30px;
        padding-top: 50px;

    }

    .city-section-one-sub-container-two-image img {

        max-width: 250px;
        max-height: 300px;
        
    }
}