.city-section-four-main-container {
  display: flex;
  flex-direction: row;
  padding: 50px 50px;
  position: relative;
  min-height: 60px;
  flex-wrap: wrap;
}

.city-section-four-main-container-circle-in-the-middle {
  position: absolute;
  top: -20px;
  left: 50%;
  background-image: url("https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692781957/City/city-section-four/circle-in-middle_gpqthj.webp");
  background-size: cover;
  background-repeat: no-repeat;
  min-width: 30px;
  min-height: 30px;
}

.city-section-four-main-container-sub-container-one {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.city-section-four-main-container-sub-container-one-heading {
  margin-bottom: 10px;
}

.city-section-four-main-container-sub-container-one-paragraph p {
  text-align: justify;
  line-height: 1.6;
}

.city-section-four-main-container-sub-container-one-button {
  margin-top: 10px;
}

.city-section-four-main-container-sub-container-one-button button {
  background-color: #612d91;
  color: #fff;
  border: none;
  font-size: medium;
  padding: 7.5px 10px;
}

.city-section-four-main-container-sub-container-two {
  flex: 1;
  display: flex;
  justify-content: center;
  align-self: center;
}

.city-section-four-main-container-sub-container-two img {
  max-width: 400px;
  max-height: 300px;
}

@media (max-width: 425px) {
  .city-section-four-main-container-circle-in-the-middle {
    top: -25px;
    left: 12.5%;
  }
}

@media (max-width: 375px) {
  .city-section-four-main-container-circle-in-the-middle {
    top: -25px;
    left: 12.5%;
  }

  .city-section-four-main-container-sub-container-two img {
    max-width: 275px;
  }
}

@media (max-width: 320px) {
  .city-section-four-main-container {
    padding: 0 30px;
    padding-top: 50px;
  }

  .city-section-four-main-container-circle-in-the-middle {
    top: 2.5px;
    left: 12.5%;
  }

  .city-section-four-main-container-sub-container-two img {
    max-width: 250px;
    max-height: 300px;
  }
}
