.cities-section-two-main-container {
  max-width: 100vw;
  min-height: calc(40vh + 130px);
  height: fit-content;
  background-color: #f3f4f9;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 50px 50px;
  position: relative;
  gap: 30px;
}

.cities-section-two-main-container-background-circle {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  background-image: url("https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692777692/City/city-section-two/circle2_1_tishfj.webp");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  min-width: 50%;
  min-height: 40vh;
}

.cities-section-two-main-container-circle-on-top-left {
  position: absolute;
  background-image: url("https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692777692/City/city-section-two/circle-on-top_dq2uza.webp");
  top: 6%;
  right: 3.75%;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  min-width: 30px;
  min-height: 30px;
}

.cities-section-two-main-container-sub-container-one {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.cities-section-two-main-container-sub-container-one-box-one {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 30px;
}

.cities-section-two-main-container-sub-container-one-box-one-button-one {
  flex: 1;
  display: flex;
  background-color: #fff;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  border-radius: 20px;
  gap: 7.5px;
  z-index: 2;
}

.cities-section-two-main-container-sub-container-one-box-one-button-one:hover {
  background-color: #612d91;
  color: #fff;
  cursor: pointer;
}

.cities-section-two-main-container-sub-container-one-box-one-button-one img {
  width: 45px;
  height: 45px;
}

.cities-section-two-main-container-sub-container-one-box-one-button-one h4 {
  font-weight: 600;
  text-align: center;
}

